.colours-wrapper {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  height: 100dvh;
}

.div0 {
  grid-area: 1 / 1 / 4 / 3;
}
.div1 {
  grid-area: 4 / 1 / 7 / 3;
}
.div2 {
  grid-area: 1 / 3 / 4 / 5;
}
.div3 {
  grid-area: 4 / 3 / 7 / 5;
}
.div4 {
  grid-area: 1 / 5 / 4 / 7;
}
.div5 {
  grid-area: 4 / 5 / 7 / 7;
}

.colour-button {
  border: none;
  cursor: pointer;
}
